import { Link, useStaticQuery, graphql } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import BackgImage from "../components/backgImage"

const Header = ({ siteTitle }) => {
  const image = useStaticQuery(graphql`
    query ImageQuery {  
      astronaut: file(sourceInstanceName: { eq: "images" }, relativePath: { eq: "eyes-2764597_640.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 1950, maxHeight:250) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <header
      //style={{
      //  marginBottom: `1.45rem`,
      //}}
    >
      <div
        style={{
          margin: `0 auto`,
          maxWidth: 960,
          padding: `1.45rem 1.0875rem`,
        }}
      >
        <BackgImage
          title="astronauta"
          fluid={image.astronaut.childImageSharp.fluid}
          overlayColor="#04040454"
        >px
          <h1 style={{ margin: 0, textAlign:"center", verticalAlign: "center"}}>
            <Link
              to="/"
              style={{
                color: `white`,
                textDecoration: `none`,
              }}
            >
              {siteTitle}
            </Link>
          </h1>
        </BackgImage>
      </div>
    </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
